// utils/chatStorage.js
export const loadChat = () => {
  const data = localStorage.getItem("chat_data");
  return data ? JSON.parse(data) : { messages: [] };
};

// senderType: "buyer" বা "admin"
// text: message text
// attachments: array
// buyerId: unique buyer id
export const addMessage = (senderType, text, attachments = [], buyerId) => {
  const data = loadChat();
  const newMsg = {
    id: Date.now() + "_" + Math.random(),
    from: senderType,
    text,
    attachments,
    buyerId,
    time: new Date().toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" })
  };
  data.messages.push(newMsg);
  localStorage.setItem("chat_data", JSON.stringify(data));
};
